@font-face {
  font-family: Roboto;
  font-weight: 500;
  src: url("https://fonts.google.com/specimen/Roboto");
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

body {
  box-sizing: border-box;
  background-color: #a09595;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo_image {
  width: 100%;
  height: 100%;
}

.nav_list {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_item {
  font-weight: 500;
}

li {
  margin-left: 20px;
  list-style: none;
}

.nav_link {
  margin: 0;
  padding: 0;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 15px;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.nav_list:hover {
  color: #00acdc;
}
